<template>
  <div class="main">
    <!-- banner图 -->
    <Banner :country="country" />
    <!-- 省份本土数据 -->
    <Localitem :localitem="localitem" v-if="dataList.length !==0" :dataList="dataList"/>
    <!-- 核酸检测机构 -->
    <Testing :title="country"/>
    <!-- 中国省份地图 -->
    <CityMap v-if="city.length !== 0" :city="city" :country="country"/>
    <!-- 疫情走势图 -->
    <ExistingChart :country="country" :diff="diff" :historylist="historylist" v-if="historylist.length !== 0"/>
    <div class="citylist main-center" v-if="diff == 0">
      <Title :title="country + '病例'" />
      <citylist :is_jwsr="is_jwsr" :citylist="citylist"/>
    </div>

    <information :diff="diff" v-if="citycode !== ''" :citycode="citycode" :country="country"/>
  </div>
</template>

<script>
import Banner from '../components/banner/banner';
import Title from '@/components/title';
import Testing from '@/components/testing';
import Citylist from './citylist'; 
import Localitem from './localitem';
import CityMap from './cityMap';
import ExistingChart from './existingChart';
import information from './information'
import { provinDataTitle, provincedataTwo } from '@/api/abroad'
export default {
  data() {
    return {
      citycode:'',
      country: '',
      localitem: [],
      citylist: [],
      dataList:[],
      city:[],
      historylist:[],
      diff:0,
      is_jwsr:''
    }
  }, 
  components: {
    Banner,
    Title,
    Citylist,
    Localitem,
    Testing,
    CityMap,
    ExistingChart,
    information
  },
  /**
 * @param {Number} diff  diff等于0为省份  等于1为市区
 * @param {string} citycode 省市的接口参数
 */
  watch:{
    $route: {
      handler (e) {
        let { citycode,diff} = e.params;
        this.getProvincedataTwo(diff,citycode);
        this.citycode = citycode;
        this.diff = diff;
      },
      immediate: true
    }
  },
  mounted() {
    this.getProvinTitle();
  },
  methods: {
    async getProvinTitle() {
      let res = await provinDataTitle();
      this.localitem = res.result.data.statistics_local_dataitem;
    },
    // 城市数据
    async getProvincedataTwo(diff,citycode) {
      let res = await provincedataTwo(diff==0?'province':'city',diff==0?'province':'citycode',citycode);
      console.log(res.data);
      let { city ,historylist,is_jwsr_history} = res.data;
      this.country = diff == 0? res.data.province :city;
      this.dataList = res.data;
      this.city = diff == 0 ? city : [];
      this.historylist = historylist;
      this.is_jwsr = is_jwsr_history;
      this.citylist = diff == 0 ? city.sort((a,b) =>b.conNum - a.conNum) : []; 
    },
    cityValue(e){
      this.getProvincedataTwo(1,e.citycode)
    }
  },
}
</script>
<style lang="less" scoped>
.main{
.citylist {
  background-color: #FFF;
}

}
</style>