<template>
  <div class="main">
    <div class="cov-title">{{country}}疫情资讯</div>
    <div class="list">
      <ul> 
        <li v-for="item in list" class="" :key="item.dataid">
          <a :href="item.url" class="d-flex d-cont-between align-item">
            <div class="title d-flex d-flex-column d-cont-between">
            <p>{{item.title}}</p>
            <span>{{item.media}}</span>
          </div>
          <div class="img" v-if="item.thumb">
            <img :src="item.thumb" alt="">
          </div>
          </a>
        </li>
      </ul>
    </div>
    <infinite-loading @infinite="infiniteHandler">
    </infinite-loading>
  </div>
</template>

<script>
import {provinceNews} from '@/api/abroad';
// 上拉加载更多
import InfiniteLoading from 'vue-infinite-loading';
export default {
  props:['citycode','country','diff'],
  data() {
    return {
      list:[],
      page:1
    }
  },
  components:{
    InfiniteLoading
  },
  mounted() {
    this.getProvinceNews();
  },
  methods: {
    async getProvinceNews($state){
      let res = await provinceNews(this.citycode,this.page,this.diff == 0?'province':'city');
      let listData = res.result.data;
      if (!$state) return;
      if (listData.length) {
        this.page += 1;
        $state.loaded();
      } else {
        $state.complete();
      }
      this.list = [...this.list, ...listData]
    },
    infiniteHandler($state) {
      this.getProvinceNews($state);
    }
  },
}
</script>
<style lang="less" scoped>
.main {
  padding:0 20px;
  .cov-title{
    font-size:32px;
    color: #4d79f3;
    font-weight: bold;
    margin-top:30px;
  }
  ul {
    li {
      
      border-bottom:2px solid #E6E6E6;
      padding:20px 0;
      min-height:154px;
      a{
        text-decoration: none;
      }
      .title{
        flex: 1;
        p{
          font-size:36px;
          line-height:48px;
          color: #111;
          overflow: hidden;
          -o-text-overflow: ellipsis;
          text-overflow: ellipsis;
          -webkit-line-clamp: 2;
          word-break: break-all;
        }
        span{
          color: #888;
        }
      }
      .img {
        width:230px;
        height:154px;
        margin-left:20px;
        img{
          font-size:0;
          height:100%;
          width:100%;
        }
      }
    }
  }
}
</style>