
<template>
  <table class="table">
    <thead>
      <tr>
        <th v-for="(item, index) in citylistTitle" :key="index" :style="`background-color:${item.bgColor}`">
          {{ item.title }}</th>
      </tr>
    </thead>
    <tbody>
      <tr class="m_item" v-for="(item, index) in citylist" @click="getIntoCity(item.citycode,is_jwsr)">
        <td>{{ item.name }} </td>
        <td>{{ item.conadd }}</td>
        <td>{{ item.conNum }}</td>
        <td>{{ item.deathNum > 0 ? item.deathNum : '' }}</td>
        <td>{{ item.cureNum }}</td>
        <td class="icon" v-if="is_jwsr === ''"><i class="iconfont icon-xiayiyeqianjinchakangengduo"></i></td>
      </tr>
    </tbody>
  </table>
</template>
<script>
export default {
  props: ['citylist','is_jwsr'],
  data() {
    return {
      citylistTitle: [
        { title: '区/县', bgColor: '#EAEAEA' },
        { title: '新增确诊', bgColor: '#FFE2DC' },
        { title: '确诊', bgColor: '#FFC9C9' },
        { title: '死亡', bgColor: '#D4D4D4' },
        { title: '治愈', bgColor: '#C0ECE0' },
        { title: '详情', bgColor: '#D4D4D4' },
      ]
    }
  },

  methods: {
    getIntoCity(e,isjwsr){
    if(isjwsr !== '') return;
    this.$router.replace({name: 'CovDomestic', params: { citycode:e, diff:1}});
    },
  },
}
</script>
<style lang="less" scoped>
.table {
  width: 714px;
  text-align: center;
  font-weight: bold;

  thead {
    tr {
      height: 42px;

      th {
        font-weight: normal;
        color: #292735;
        font-size: 22px;
      }
    }
  }

  tbody {
    tr {}
  }

  .m_item {

    border-bottom: solid 2px #f3f3f3;
    font-size: 24px;

    td {
      padding: 20px 0;
    }

    td:first-child {
      width: 140px;
      padding: 20px;
    }

    td.icon{
      color: #C9C9C9;
    }
  }
}
</style>