<template>
  <div class="map main-center">
    <Title :title="`${country}${type == 0 ?'现存':'累计'}确诊疫情地图`" />
    <div id="cityMapBox"></div>
    <div class="btns d-flex d-cont-between">
      <button @click="switching(0)" :class="[type==0 ? 'active':'']">现存数据</button>
      <button @click="switching(1)" :class="[type==1 ? 'active':'']">累计数据</button>
    </div>
  </div>
</template>

<script>
import { pinyin } from 'pinyin-pro';
import Title from '@/components/title'
export default {
  props: ['city', 'country'],
  data() {
    return {
      type:0
    }
  },
  mounted() {
    this.getcitydata(this.type);
  },
  components:{
    Title
  },
  methods: {
    getcitydata(type) {

      let cityitem = [];
      this.city.forEach(item => {

        item.citycode !== '' && cityitem.push({
          name: item.mapName,
          value:type == 0 ? parseInt( item.econNum ) : parseInt(item.conNum)
        })
      });
      this.$myecharts.cityMap(
        'cityMapBox',
        this.country,
        pinyin(this.country, { toneType: 'none' }).replace(/\s*/g, ""),
        cityitem
      )
    },
    switching(e){
      this.type = e;
      this.getcitydata(e);
    }
  },
}
</script>
<style lang="less" scoped>
.map {
  background-color: #FFF;
  margin-bottom: 30px;
  padding-bottom: 30px;

  #cityMapBox {
    width: 714px;
    height: 600px;
  }

  .btns {
    margin-top: 20px;
    padding: 0 20px;

    button {
      background: none;
      text-align: center;
      font-size: 24px;
      border-radius: 10px;
      color: #9a9ead;
      height: 60px;
      flex: 1;
      border: 2px solid #d7deeb;
      margin-right: 16px;
      position:relative;
    }

    .active {
      background: #f1f4ff;
      color: #4d79f3 !important;
      border-color: #bdcfff;
      font-weight: bold;
    }

    .active::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 1px solid #BDCFFF;
      border-width: 0 0 1px 1px;
      display: block;
      top: -8px;
      left: 50%;
      margin-left: -3px;
      background: #f1f4ff;
      transform: rotate(135deg);
      width: 10px;
      height: 10px;
    
  }
}
}
</style>