
<template>
  <div class="main main-center">
    <div class="d-flex d-cont-between align-item">
      <Title :title="country + titles[type]" />
      <p v-if="diff==0" class="daysNum d-flex d-cont-arund align-item">
        <span :class="diffNum === 0 ? 'active' : ''" @click="onpick(0)">30天</span> |
        <span :class="diffNum === 1 ? 'active' : ''" @click="onpick(1)">全部</span>
      </p>
    </div>
    <div id="outbound"></div>
    <div v-if="diff==0" class="tbas d-flex d-cont-between">
      <p :class="type === item.type ? 'active' : ''" v-for="(item, index) in topTenTabs" @click="checkType(item.type)">
        <span>{{ item.toptitle }}</span><br />
        <span>{{ item.text }}</span>
      </p>
    </div>
  </div>
</template>

<script>
import { exi } from './chartData';
import Title from '@/components/title';
import { extract30 } from '@/utils/extract30';
export default {
  props: ['historylist', 'country','diff'],
  data() {
    return {
      topTenTabs: [
        { type: 0, text: '无症状 | 确诊 | 死亡 | 治愈', toptitle: '新增' },
        { type: 1, text: '确诊|死亡|治愈', toptitle: '累计' },
        { type: 2, text: '新增|累计确诊', toptitle: '境外输入' },
      ],
      titles: ['疫情新增趋势图 ', '疫情趋势图 ', '境外输入趋势图'],
      type: 0,
      diffNum: 0,
    }
  },
  watch:{
    diff:{
      handler(e){
        console.log(e);
        this.checkType(0);
      }
    }
  },
  mounted() {
    this.checkType(0);
  },
  components: {
    Title
  },
  methods: {
    checkType(type) {
      this.type = type;
      this.processing(type);
    },
    processing(type) {
      let list = [];
      if(this.diff == 0){
        list = this.diffNum == 0 ? extract30(this.historylist,30) : this.historylist;
      }else{
        list = extract30(this.historylist,7);
      }
      let date = list.map(item => item.date);
      let myData = exi(type, list,this.diff);
      this.$myecharts.mainlandMap("outbound", myData, date, true, '人数', true)
    },
    onpick(e) {
      this.diffNum = e;
      this.checkType(this.type);
    },
  },
}
</script>
<style lang="less" scoped>
.main {
  background-color: #FFF;
  margin-bottom:30px;
  padding-bottom:30px;
  #outbound {
    width: 714px;
    height: 714px;
  }
  .daysNum {
    font-size: 20px;
    color: #ccc;
    height: 60px;
    margin-top: 10px;
    text-align: center;
    flex-direction: row;
    width: 200px;
    border: 1px solid rgba(204, 204, 204, 0.6);
    border-radius: 30px;
    margin-left: auto;
    margin-right: 10px;

    span {
      font-size: 26px;
      color: #444;
    }

    span.active {
      color: #3192F1;
    }
  }

  .tbas {
    margin-top: 30px;

    p {
      background: #fff;
      text-align: center;
      font-size: 24px;
      line-height: 40px;
      border-radius: 10px;
      position: relative;
      padding: 12px 0;
      color: #808595;
      margin: 0 10px;
      flex: 1 1 auto;
      border: 2px solid #D7DEEB;
      position: relative;
    }

    .active {
      background: #E0E8FF;
      color: #4D79F3 !important;
      border-color: #BDCFFF;
    }

    .active::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 2px solid #BDCFFF;
      border-width: 0 0 2px 2px;
      display: block;
      top: -10px;
      left: 50%;
      margin-left: -8px;
      background: #E0E8FF;
      transform: rotate(135deg);
      width: 10px;
      height: 10px;
    }
  }
}
</style>