export function exi(type, list,diff) {
  let covnum = diff ==0 ?[4, 3, 2] :[3];
  let obj = null;
  if(diff == 0){
    obj = {
      0: [
        { 
          name: '无症状', 
          color: '#8A38A1', 
          data: list.map(item => item.locAsymNum) 
        },
        { 
          name: '确诊', 
          color: '#CF3313', 
          data: list.map(item => item.conadd) 
        },
        { 
          name: '死亡', 
          color: '#2F2F2F', 
          data: list.map(item => item.deathNum) 
        },
        { 
          name: '治愈', 
          color: '#13B593', 
          data: list.map(item => item.cureadd) 
        }
      ],
      1: [
        { 
          name: '确诊', 
          color: '#CF3313',
          data: list.map(item => item.conNum) 
        },
        { 
          name: '死亡', 
          color: '#2F2F2F',
          data: list.map(item => item.deathNum) 
        },
        { 
          name: '治愈', 
          color: '#13B593',
          data: list.map(item => item.cureNum) 
        }
      ],
      2: [
        { 
          name: '新增输入', 
          color: '#FF3535',
          data: list.map(item => item.jwsradd) 
        },
        { 
          name: '累计输入', 
          color: '#74011A',
          data: list.map(item => item.jwsrNum) 
        }],
    }
  }else{
    obj = {
      0: [
        { 
          name: '新增本土确诊', 
          color: '#A48256',
          data: list.map(item => item.locIncrNum) 
        },
        { 
          name: '新增本土无症状', 
          color: '#A64CBF',
          data: list.map(item => item.locAsymNum) 
        },
        { 
          name: '现存确诊', 
          color: '#CF3313',
          data: list.map(item => item.econNum) 
        }
      ],
    }
  }
  let existingArr = [];
  for (let i = 0; i < covnum[type]; i++) {
    existingArr.push({
      name: obj[type][i].name,
      color: obj[type][i].color,
      data: obj[type][i].data,
      type: 'line',
      smooth: true,
      symbolSize: 6,
      symbol: "circle",
			connectNulls:true
    })

  }

  return existingArr
}