
<template>
  <div class="main">
    <van-collapse class="van-collapse" v-for="(item, index) in cities_cn" v-model="activeNames">
      <van-collapse-item :name="index">
        <template #title>
          <div class="plagueName">{{ item.c }}</div>
        </template>
        <div v-if="item.z.length !== 0" class="plagueList d-flex d-cont-start">
          <span 
          @click="regionSelect(item.e)" 
          class="city_link">{{ item.c }}全省</span>
          <span 
          v-for="items in item.z" 
          class="city_link"
          @click="regionSelect(items.i)"
          >
            {{ items.c }}
          </span>
        </div>
        <div v-else class="plagueList">
          <span @click="regionSelect(item.e)" class="city_link">{{item.c}}</span>
        </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
export default {
  props:['cities_cn'],
  data() {
    return {
      activeNames: ['1'],
    }
  },
  methods: {
    regionSelect(e){
      this.$bus.$emit('nocityCode',e);
    }
  },
}
</script>
<style lang="less" scoped>
.van-collapse{
  .van-collapse-item{
    /deep/ .van-cell{ padding:0 !important;}
    /deep/ .van-icon{margin-top:40px;}
  }
}
 
.main{
  padding:0 30px;
  .plagueName{
    height:100px;
    line-height:100px;
    border-bottom: 2px solid #F2F2F2;
  }
  .plagueList{
    flex-wrap: wrap;
    span{
      width: 30%;
      display: block;
      margin: 0 3% 24px 0;
      height: 60px;
      text-align: center;
      background: #F0F0F0;
      border-radius:30px;
      font-size:28px;
      color: #111;
      line-height:60px;
      padding: 0 10px;
      box-sizing: border-box;
    }
  }
  .van-collapse:last-child{
    .plagueName{
    border-bottom:none;
  }
  }
}
</style>