
<template>
  <div class="banner d-flex d-cont-end">
    <!-- <div class="btns">
      <button @click="region"><i class="iconfont icon-ditu_dingwei_o"></i> 美国 <i
          class="iconfont icon-xiala2"></i></button>
      <button>抗议频道 <i class="iconfont icon-xiayiyeqianjinchakangengduo"></i></button>
    </div> -->
    <h1 class="slideTit">新冠肺炎疫情</h1>
    <h3 class="slideSubTit">{{country}}实时动态追踪</h3>

    <van-popup v-model="show" round position="bottom" class="van-popup">
      <div class="headerChoice">
        <div class="headerIcon">
          <div class="title  d-flex d-cont-between align-item">
            <span @click="show = false">取消</span>
            <span class="title-center">地区选择</span>
            <span></span>
          </div>
          <div class="currentPosi">
            <span class="d-flex d-cont-start align-item"> <i class="iconfont icon-ditu_dingwei_o"></i> 当前定位：北京</span>
          </div>
          <div class="regionBtns d-flex d-cont-center align-item">
            <button @click="dataSwitching(1)" :class="['country', active == 1 ? 'active' : '']">国内</button>
            <button @click="dataSwitching(2)" :class="['country', active == 2 ? 'active' : '']">国外</button>
          </div>
        </div>
        <div class="content">
          
        <domestic v-show="active==1" :cities_cn="cities_cn"/>
        <berAbroad v-show="active==2" :countries="countries" />
        
      </div>
      </div>
      
    </van-popup>
  </div>
</template>

<script>
import { banner } from '@/api/abroad';
import domestic from './domestic'
import berAbroad from './berAbroad'
export default {
  props:['country'],
  data() {
    return {
      show: false,
      active: 1,
      cities_cn: [],
      countries: [],
      
    }
  },
  created(){
    
  },
  mounted() {
    this.getBanner();
    this.$nextTick(() => this.$bus.$on('nocityCode',e=>{
      this.show = false;
    }))
  },
  components:{
    domestic,
    berAbroad
  },
  methods: {
    async getBanner() {
      let res = await banner();

      this.cities_cn = res.data.cities_cn;
      this.countries = res.data.countries;
    },
    region() {
      this.show = true
    },
    dataSwitching(e) {
      this.active = e;
    }
  },
  destroyed() {
    this.$bus.$off('nocityCode')
  },
}
</script>
<style lang="less" scoped>
.banner {
  width: 100%;
  background-image: url('@/assets/images/banner.png');
  padding-top: 280px;
  background-size: cover;
  position: relative;
  .van-popup{
    overflow: hidden;
    height:1000px;
  }
  .btns {
    position: absolute;
    top: 0;

    button {
      height: 54px;
      line-height: 54px;
      background: rgba(0, 0, 0, .2) !important;
      border-radius: 30px;
      color: #FFF;
      padding: 0 20px;
      margin-right: 20px;
      margin-top: 30px;
    }
  }

  .slideTit {
    color: #fff;
    font-size: 24px;
    top: 85px;
    position: absolute;
    font-weight: normal;
    left: 40px;
  }

  .slideSubTit {
    position: absolute;
    font-size: 50px;
    color: #fff;
    text-shadow: 0 4px 0 #789ce9;
    top: 120px;
    left: 40px;
    font-weight: normal;
  }

  .headerChoice {
    position: relative;
    padding-top:310px;
    .headerIcon {
      position: absolute;
      top: 0;
      .title {
        font-size: 34px;
        width: 750px;
        padding: 0 20px;
        height: 110px;
        border-bottom: 10px solid #F7F7F7;

        span {
          flex: 1;
          color: #fe350e;
        }

        span.title-center {
          text-align: center;
          color: #111;
        }
      }

      .currentPosi {
        padding: 0 20px;
        height: 80px;
        line-height: 80px;
        font-size: 30px;

        span {
          i {
            font-size: 40px;
            margin-right: 20px;
          }
        }
      }

      .regionBtns {
        height: 100px;
        border-top: 10px solid #F7F7F7;

        .country {
          display: inline-block;
          line-height: 60px;
          height: 60px;
          padding: 0 38px;
          background: #fff;
          border-radius: 12px;
          font-size: 24px;
          color: #a8abb8;
          margin: 0 10px;
          border: 2px solid #dee4ee;
        }

        .active {
          background: #f1f4ff;
          border-color: #c7d6fe;
          color: #5f87f5;
        }
      }
    }
    .content{
      height:800px;
      overflow-y: scroll;
      padding-bottom:100px;
    }
  }
}
</style>