import http  from '@/http/index';
export const abroad = (cityCode) => http.get("/news/wap/ncp_foreign.d.json?citycode=" + cityCode);
// 中英文对照表
export const comparison = () => http.get("/comparison.json");
// abroad banner接口
export const banner = () => http.get("/sinawap_get_area_tree.d.json");
// 资讯接口 
export const informa = (citycode,page) => http.get(`
/news/wap/fyzt_newsv2.d.json?type=country&code=${citycode}&page=${page}
`); 
// 国内接口

export const provinDataTitle = () => http.get(`/news/fy_config.d.json?env=local`);

export const provincedataTwo = (region,regionCode,citycode) => http.get(`/news/ncp/data.d.json?mod=${region}&${regionCode}=${citycode}`); 


export const provinceNews = (citycode,page,region) => http.get(`/news/wap/fyzt_newsv2.d.json?type=${region}&code=${citycode}&page=${page}`); 


